.flex{
  display: flex;
}
.credit{
  cursor: pointer;
  background-color: black;
  display: flex;
color: rgb(255, 255, 255);
border-radius: 10px;
  width: 170px;
  height: 80px;
  position: relative;
 top: 56px;
 text-align: center;
 .icon{
  position: relative;
  margin-left: 10px;
  margin-right: 10px;
  margin-top: 30px;
 }
 h1{
  margin-top: 30px;
 }
}


.cart-itm {
    background: #f6f9fc;
    height: auto;
    padding: 50px 0;
    h4 span {
        color: #e94560;
        margin-left: 20px;
        font-weight: 500;
      }
    

    .img {
        width: 150px;
        height: 150px;
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }

      
    .cart_details {
        width: 70%;
        h3 {
            font-size: 20px;
            font-weight: 500;
            margin-top: 20px;
          }
          h4 {
            font-size: 15px;
            font-weight: 400;
            margin-top: 50px;
            color: grey;
          }



      }
     
  }
  .inCart {
    background: none;
    border: 1px solid rgb(3 0 71 / 9%);
    color: #e94560;
    cursor: pointer;
  }
  .desCart {
    background: #f6f9fc;
  }
  
  .cart-list {
    background: white;
    margin-top: 30px;
  }

  .cart-total {
    width: 30%;
    margin-top: 30px;
    margin-left: 30px;
    height: 130px;
    h4 {
        font-size: 15px;
        font-weight: 400;
      }
    h3 {
        font-size: 20px;
        font-weight: 500;
        color: #e94560;
      }
    h2 {
        font-size: 18px;
        margin-bottom: 20px;
        border-bottom: 1px solid rgb(3 0 71 / 9%);
        padding-bottom: 10px;
        color: #e94560;
      }
    }
    .no-items {
        color: #e94560;
        font-size: 18px;
        margin-top: 30px;
        height: 130px;
      }

.removeCart {
        background: none;
        font-size: 25px;
        text-align: right;
        margin-right: 10px;
        cursor: pointer;
}
.cartControl {
        margin-top: 50px;
        button {
            width: 40px;
            height: 40px;
            margin: 10px;
            border-radius: 5px;
            font-size: 20px;
          }     
      }
    

     